// import React from "react";
import React, { useState } from "react";

export default function Textform(props) {
  const handleUpClick = () => {
    let newText = text.toUpperCase();
    setText(newText);
    console.log("clicked");
  };
  const handleOnChange = (event) => {
    console.log("handleOnchange");
    setText(event.target.value);
  };
  const handleLoClick = () => {
    let newText = text.toLowerCase();
    setText(newText);
    console.log("clicked");
  };
  const handleWhiteSpace = () => {
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
  };

  const handleCopy = () => {
    var text = document.getElementById("mybox");
    text.select();
    text.setSelectionRange(0, 1000);
    navigator.clipboard.writeText(text.value);
  };

  const handleProperCase = () => {
    let words = text.split(" ");
    let CapitalizeWords = [];
    words.forEach((element) => {
      CapitalizeWords.push(
        element[0].toUpperCase() + element.slice(1, element.length)
      );
    });
    setText(CapitalizeWords.join(" "));
  };

  const clearText=()=>{
    setText("")
  }
  const [text, setText] = useState("");

  return (
    <>
    <div className="container my-5">
      <div className="mb-3">
        
          <h1>{props.title}</h1>
        
        <textarea
          className="form-control"
          id="mybox"
          rows="8"
          value={text}
          onChange={handleOnChange}
        ></textarea>
      </div>
      <button
        onClick={handleUpClick}
        type="button"
        class="btn btn-primary mx-1 my-1"
      >
        UPPER CASE
      </button>
      <button
        onClick={handleLoClick}
        type="button"
        class="btn btn-primary mx-1 my-1"
      >
        {" "}
        LOWER CASE
      </button>
      <button
        onClick={handleWhiteSpace}
        type="button"
        class="btn btn-primary mx-1 my-1"
      >
        REMOVE SPACES
      </button>
      <button
        onClick={handleProperCase}
        type="button"
        class="btn btn-primary mx-1 my-1"
      >
        {" "}
        PROPER CASE
      </button>
      <button
        onClick={handleCopy}
        type="button"
        class="btn btn-primary mx-1 my-1"
      >
        COPY TEXT
      </button>
      <button
        onClick={clearText}
        type="button"
        class="btn btn-primary mx-1 my-1"
      >
        CLEAR TEXT
      </button>
    </div>
    <div className="container">
      <h4>TEXT LENGTH</h4>
      <p>Total {text.split("").filter((element)=>{return element.length!==0}).length}words</p>
      <h5>PREVIEW</h5>
      <p>{text.length>0?text:"enter your text preview"}</p>

      </div>
    </>
  );
}
